import React, { Component } from 'react'
import { Grid, Input, Icon, Form, Radio } from 'semantic-ui-react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';



export default class Details extends Component {
    state = {}
    handleChange = (e, { value }) => this.setState({ value })

    disableIn = (value) => {
        switch (this.props.step) {
            case 2:
                if (value === 1) return 'disabled'
                else if (value === 2) return 'enabled'
                else return 'disabled'
            case 3:
                if (value === 1) return 'disabled'
                else if (value === 2) return 'disabled'
                else return 'enabled'
            default:
                if (value === 1) return 'enabled'
                else if (value === 2) return 'disabled'
                else return 'disabled'
        }
    }

    render() {
        console.log(this.props.step)
        return (
            <Grid>
                <Grid.Row>
                    <Grid.Column width={5}>
                        <Input className={this.disableIn(1)} icon='users' iconPosition='left' placeholder='First Name' style={{ paddingTop: 10 }} /> <br />
                        <Input className={this.disableIn(1)} icon='users' iconPosition='left' placeholder='Last Name' style={{ paddingTop: 10 }} /><br />
                        <Input className={this.disableIn(1)} iconPosition='left' placeholder='Email' style={{ paddingTop: 10 }}>
                            <Icon name='at' />
                            <input />
                        </Input>
                        <Input className={this.disableIn(1)} icon='users' iconPosition='left' placeholder='Contact: +63' style={{ paddingTop: 10 }} /> <br />
                    </Grid.Column>
                    <Grid.Column width={5} style={{ 'text-align': 'left', paddingLeft: 80 }}>
                        <Form.Field>
                            Choose payment method: <b>{this.state.value}</b>
                        </Form.Field>
                        <Form.Field>
                            <Radio
                                label='Mastercard'
                                name='radioGroup'
                                value='Mastercard'
                                checked={this.state.value === 'Mastercard'}
                                onChange={this.handleChange}
                               
                            />
                        </Form.Field>
                        <Form.Field>
                            <Radio
                                label='Visa'
                                name='radioGroup'
                                value='Visa'
                                checked={this.state.value === 'Visa'}
                                onChange={this.handleChange}
                                
                            />
                        </Form.Field>
                        <Form.Field>
                            <Radio
                                label='Paypal'
                                name='radioGroup'
                                value='Paypal'
                                checked={this.state.value === 'Paypal'}
                                onChange={this.handleChange}
                               
                            />
                        </Form.Field>
                        <Form.Field>
                            <Radio
                                label='Cash'
                                name='radioGroup'
                                value='Cash'
                                checked={this.state.value === 'Cash'}
                                onChange={this.handleChange}
                                
                            />
                        </Form.Field>
                    </Grid.Column>
                    <Grid.Column width={5} style={{'text-align': "right"}}>
                        <Grid>
                           <Grid.Row>
                           <Grid.Column width={8} style={{ 'text-align': 'right' }}>
                        
                      </Grid.Column>
                      <Grid.Column width={8} style={{ 'text-align': 'right' }}>
                      <img src="images/google-play-badge.png" style={{ width: 180, height: 86, cursor: 'pointer' }} />
                        <img src="images/apple.png" style={{ width: 160, height: 60, marginTop: 13, cursor: 'pointer' }} />
                      </Grid.Column>
                           </Grid.Row>
                        </Grid>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
        )

    }
}